<template>
    <div class="content">
        <template v-if="playPl2">
            <div class="fr-layout">
                <div class="play29">
                    <ul class="mui-scroll play3">
                        <li @click="selectTouzhu" v-for="(item1, index1) in playPl2" :key="index1">
                            <strong>{{item1.name}}</strong>
                            <span>{{item1.playPl}}</span>
                            <input hidden class="name" :value="'特码B-' + item1.name" type="text">
                            <input hidden class="playPl" :value="item1.playPl" type="text">
                            <input hidden class="playPlId" :value="item1.playPlId" type="text">
                        </li>
                    </ul>
                </div>
            </div>
        </template>
        <loadding v-else></loadding>
    </div>
    
</template>

<script>

    import { mapState } from "vuex";
    import { 
        GAME_GET_SSC_PLAY_PL2
    } from "@/tool/common/constants.js";

    import $ from "jquery";

    export default {
        name: 'Play1',
        props:['playId'],
        data(){
            return {
                left: 0,
                init:0,
                playPl2: false,
                load: false,
            }
        },
        created(){
            this.getSscPlayPl2()
        },
        computed: {
            ...mapState('Game', {
                playGroupId: state => state.playGroupId,
                number: state => state.getSscOpenTime2.number,
                chip: state => state.chip,
            })
        },
        activated(){
            if(this.load){
                console.log(2);
                setTimeout(() => {
                    this.mui('.play29').scroll().scrollTo(0, 0, 100) // 100毫秒滚动到顶
                }, 200);
            }
        },
        mounted(){
            
        },
        methods: {
            // 滑动切换
            onChange(index) {
                this.left = index
                setTimeout(() => {
                    this.init = index
                }, 500);
            },
            // 切换类型
            toggleType(index){
                this.$refs.swipe.swipeTo(index)
                this.left = index
            },
            // 选择投注码
            selectTouzhu(e){
                var $even = e.target.parentNode.classList
                if($even.contains('active')){
                     $even.remove('active')
                } else{
                    $even.add('active')
                }
                this.sscBetList()
            },
            // 所选注数
            sscBetList(){
                var _this = this
                var sscBetList = []
                $('li.active').each(function(){
                    var playPlId = $(this).find(".playPlId").val()
                    var playPl = $(this).find(".playPl").val()
                    var name = $(this).find(".name").val()
                    var zhushu = 1
                    var zhu = {
                        "number": _this.number,
                        "playGroupId": _this.playGroupId,
                        "beishu":1,
                        "zhushu":zhushu,
                        "content": name,	
                        "playId": _this.playId,
                        "playPl":playPl,
                        "playPlId":playPlId,
                        "totalMoney":_this.chip,
                        "perMoney":_this.chip * zhushu,
                    }
                    sscBetList.push(zhu)
                })
                this.$store.commit('Game/SET_SSC_BET_LIST', sscBetList)
            },
            playPl2_filter(sscPlayPlList){
                var filter = ['红波', '蓝波', '绿波']
                var result = []

                filter.forEach(field => {
                    sscPlayPlList.forEach(item1 => {
                        if(field == item1.name){
                            result.push(item1)
                        }
                    })
                })
                console.log(result);
                return result
            },
            // 当前玩法赔率
            getSscPlayPl2(){
                var data = {
                    playId: this.playId
                }
                this.$api.game[GAME_GET_SSC_PLAY_PL2](data)
                .then(res=>{
                    console.log(res);
                    this.playPl2 = this.playPl2_filter(res.sscPlayPlGroupList[1]['sscPlayPlList'])
                    this.$nextTick(()=>{
                        this.mui(".play29").scroll({
                            deceleration:0.0005
                        })
                        this.load = true
                    })
                })
            },
        }
    }
</script>

